/**
 * api接口统一管理
 */

import axios from './http'; // 导入http中创建的axios实例
import qs from 'qs'; // 根据需求是否导入qs模块
const api_url = {
  hosturl: '',
}

const ssreader_ajax_obj = {
  search(params) {
    return axios.post(`${api_url.hosturl}/api/v1/search`, qs.stringify(params));
  },
  chapterlist(params) {
    return axios.post(`${api_url.hosturl}/api/v1/chapterlist`, qs.stringify(params));
  },
  chapter(params) {
    return axios.post(`${api_url.hosturl}/api/v1/chapter`, qs.stringify(params));
  }
}
export default ssreader_ajax_obj;



